:root {
  --background-colour: #7395ae;
  --header-colour: #557a95;
  --footer-colour: #557a95;
  --menu-colour: #c5c6c7;

  --title-text-colour: white;
  --text-colour: #000000;
  --footer-text-colour: white;

  --content-link-colour: linear-gradient(45deg, #d1dbdf, #a9cedd, #8dc5db, #73c2e0);
  --content-colour: #73c2e0;

  --content-link-size: 1.4rem;

  --links-content-link: rgba(24, 59, 68, 0.6);
  --links-content-visited: rgba(24, 59, 68, 0.6);
  --links-content-hover: rgba(24, 59, 68, 0.6);
  --links-content-active: rgba(255, 0, 0, 0.6);

  --links-colour-link: rgb(87, 150, 223);
  --links-colour-visited: rgb(15, 32, 109);
  --links-colour-hover: red;
  --links-colour-active: rgb(211, 115, 25);

  --button-border-colour: black;

  --header-height: 3rem;
  --footer-height: 4rem;

  --table-spacing: 40px 5px;
}

:root[data-theme="dark"] {
  --background-colour: #1f2833;
  --header-colour: #0b0c10;
  --footer-colour: #0b0c10;
  --menu-colour: #5d5c61;

  --title-text-colour: #66fcf1;
  --text-colour: #66fcf1;
  --footer-text-colour: #66fcf1;

  --content-link-colour: linear-gradient(45deg, #45A29E, #30837f, #1b797a, #116466);
  --content-colour: #116466;

  --links-content-link: rgba(255, 0, 0, 0.6);
  --links-content-visited: rgba(255, 0, 0, 0.6);
  --links-content-hover: rgba(255, 0, 0, 0.6);
  --links-content-active: rgba(211, 115, 25, 0.6);

  --links-colour-link: rgb(87, 150, 223);
  --links-colour-visited: rgb(61, 126, 201);
  --links-colour-hover: red;
  --links-colour-active: rgb(211, 115, 25);

  --button-border-colour: white; 
}

:root[data-theme="light"] {
  --background-colour: #7395ae;
  --header-colour: #557a95;
  --footer-colour: #557a95;
  --menu-colour: #c5c6c7;
  
  --title-text-colour: white;
  --text-colour: #557a95;
  --footer-text-colour: white;

  --content-link-colour: linear-gradient(45deg, #d1dbdf, #a9cedd, #8dc5db, #73c2e0);
  --content-colour: #d1dbdf;

  --links-content-link: rgba(24, 59, 68, 0.6);
  --links-content-visited: rgba(24, 59, 68, 0.6);
  --links-content-hover: rgba(24, 59, 68, 0.6);
  --links-content-active: rgba(255, 0, 0, 0.6);

  --links-colour-link: #254e58;
  --links-colour-visited: #183b44;
  --links-colour-hover: red;
  --links-colour-active: rgb(211, 115, 25);

  --button-border-colour: black;

}

#main-nav {
  --width-small: 100px;
  --width-negative-small: -100px;
  --width: 200px;
  --width-negative: -200px;
}